.product-card {
  color: #060b35;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  max-width: 200px;
  min-height: 408px;
  padding: 32px 16px 16px;
}

.product-card__image-container {
  justify-content: center;
  width: 100%;
  margin-bottom: 40px;
  display: flex;
}

.product-card__image {
  width: 160px;
  height: 134px;
}

.product-card__title {
  text-align: left;
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product-card__code {
  text-align: left;
  color: #616070;
  margin-top: 4px;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.product-card__reviews {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.product-card__price {
  justify-content: space-between;
  align-items: center;
  margin-top: 24px;
  margin-bottom: 16px;
  display: flex;
}

.product-card__reviews-text {
  color: #060b35;
  margin-left: 8px;
  font-size: 10px;
}

.product-card__price-label {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.product-card__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.product-card__buy-button {
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  background: #00acdc;
  border: none;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  padding: 8px 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
}

.product-card__buy-button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  justify-content: space-around;
  align-items: center;
  width: 96px;
  height: 16px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 13px;
  height: 12px;
  margin-right: 5px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-size: 16px;
}
